@font-face {
  font-family: "ArialMT";
  src: url("../fonts/arialmt/arialmt-regular.woff") format("woff"), url("../fonts/arialmt/arialmt-regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "GnuolaneFree";
  src: url("../fonts/gnuolanefree/gnuolanefree-regular.woff") format("woff"), url("../fonts/gnuolanefree/gnuolanefree-regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
*,
:after,
:before {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  background: #fafafa;
  font: 400 14px/24px "ArialMT", sans-serif;
  color: #747474;
}